@use '../abstract/media_queries' as mq;

.head{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Space Mono', monospace;
  background-color: black;
  color: white;
  padding: 0.5rem 5rem;

  @include mq.responsive('xl')
  {
    padding: 0.5rem 4rem;
  }
  @include mq.responsive('lg')
  {
    padding: 0.5rem 3rem;
  }
  @include mq.responsive('xs')
  {
    padding: 0.5rem 2rem;
  }

  &_title{
    text-decoration: none;
    color: white;
  }

  &_admin{
    text-decoration: none;
    color: white;

    @include mq.responsive('lg')
    {
      display: none;
    }
  }
  
  &_menu{
    display: none;    
    position: relative;
    background-color: black;
    border: none;

    @include mq.responsive('lg')
    {
      display: inline-block;
    } 

    &_icon{
      color: white;
      font-size: 1rem;
      cursor: pointer;
    }
  }
}